// src/components/Summary.tsx
import React from "react";
import { SurveyData, Area } from "./types";
import Section from "./common/Section";
import Actions from "./common/Actions";
import Button from "./common/Button";
import { buildSummary, buildWordContent } from "./helpers";
import { Document, Packer, Paragraph, Table } from "docx";
import { saveAs } from "file-saver";

interface SummaryProps {
  data: SurveyData;
  onPrev: () => void;
  onReset: () => void;
}

export default function Summary({ data, onPrev, onReset }: SummaryProps) {
  const handleDownload = async () => {
    // Load logo from public folder
    let logoBuffer: ArrayBuffer | undefined;
    try {
      const response = await fetch("/Gijima-Logo.png");
      if (response.ok) {
        logoBuffer = await response.arrayBuffer();
      }
    } catch (err) {
      console.warn("Logo not found, continuing without it.");
    }

    // Build Word content
    const wordChildren: (Paragraph | Table)[] = await buildWordContent(data, logoBuffer);

    const doc = new Document({
      sections: [
        {
          properties: {},
          children: wordChildren,
        },
      ],
    });

    const blob = await Packer.toBlob(doc);
    saveAs(blob, "survey-summary.docx");
  };

  // Recursive rendering for hierarchical areas
  const renderAreas = (areas: Area[], prefix = "", seen = new WeakSet()): React.ReactNode[] => {
    if (!areas || areas.length === 0) return [];
    return areas.flatMap((ar, i) => {
      if (seen.has(ar)) return [];
      seen.add(ar);

      const numbering = prefix ? `${prefix}.${i + 1}` : `${i + 1}`;
      const thisItem = (
        <li key={numbering + ar.name}>
          <span className="font-semibold">{numbering}. {ar.name}</span>
          {ar.noiseLevelDb && <span className="text-sm text-gray-600">  {ar.noiseLevelDb} dB ({ar.noiseType || "N/A"})</span>}
          {ar.shiftDuration && <span className="text-sm text-gray-600"> | Shift: {ar.shiftDuration}h</span>}
          {ar.exposureTime && <span className="text-sm text-gray-600"> | Exposure: {ar.exposureTime}h</span>}
          {ar.notes && <span className="text-sm text-gray-500"> | Notes: {ar.notes}</span>}
        </li>
      );

      const children = ar.subAreas ? renderAreas(ar.subAreas, numbering, seen) : [];
      return [thisItem, ...children];
    });
  };

  return (
    <Section title="Summary">
      {/* Plain text preview */}
      <pre className="whitespace-pre-wrap bg-gray-50 p-4 rounded-md text-sm">
        {buildSummary(data)}
      </pre>

      {/* Hierarchical tree preview */}
      {data.areas && data.areas.length > 0 && (
        <div className="mt-4">
          <h3 className="font-semibold mb-2">Areas (Hierarchical)</h3>
          <ul className="list-disc ml-6">
            {renderAreas([...data.areas])}
          </ul>
        </div>
      )}

      <Actions>
        <Button variant="secondary" onClick={onPrev}>Back</Button>
        <Button variant="secondary" onClick={onReset}>Reset</Button>
        <Button onClick={handleDownload} variant="success">Download Word</Button>
      </Actions>
    </Section>
  );
}








