// src/components/NoiseSourcesForm.tsx
import React from "react";
import Button from "./common/Button";

interface NoiseEntry {
  source: string;
  description: string;
  mit: string;
  type: string;
}

interface NoiseSourcesFormProps {
  normalConditions: "Yes" | "No";
  setNormalConditions: React.Dispatch<React.SetStateAction<"Yes" | "No">>;
  noiseEntry: NoiseEntry;
  setNoiseEntry: React.Dispatch<React.SetStateAction<NoiseEntry>>;
  noiseSources: NoiseEntry[];
  setNoiseSources: React.Dispatch<React.SetStateAction<NoiseEntry[]>>;
  onNext: () => void;
  selectedAreaPath?: { main: number; sub?: number; ss?: number } | null;
}

export default function NoiseSourcesForm({
  normalConditions,
  setNormalConditions,
  noiseEntry,
  setNoiseEntry,
  noiseSources,
  setNoiseSources,
  onNext,
  selectedAreaPath,
}: NoiseSourcesFormProps) {
  const handleAddSource = () => {
    if (!noiseEntry.source || !noiseEntry.description || !noiseEntry.mit) return;
    if (normalConditions === "Yes" && !noiseEntry.type.trim()) return;
    setNoiseSources([...noiseSources, noiseEntry]);
    setNoiseEntry({ source: "", description: "", mit: "", type: "" });
  };

  const handleDelete = (index: number) => {
    const updated = [...noiseSources];
    updated.splice(index, 1);
    setNoiseSources(updated);
  };

  const handleEdit = (index: number) => {
    const entry = noiseSources[index];
    setNoiseEntry(entry);
    handleDelete(index);
  };

  const isAddEnabled =
    noiseEntry.source.trim() &&
    noiseEntry.description.trim() &&
    noiseEntry.mit.trim() &&
    (normalConditions === "No" || noiseEntry.type.trim());

  return (
    <div className="space-y-4">
      {/* Optional Area Info */}
      {selectedAreaPath && (
        <p className="text-sm text-gray-500">
          Editing area: Main {selectedAreaPath.main}
          {selectedAreaPath.sub !== undefined ? ` > Sub ${selectedAreaPath.sub}` : ""}
          {selectedAreaPath.ss !== undefined ? ` > Sub-Sub ${selectedAreaPath.ss}` : ""}
        </p>
      )}

      {/* Normal Conditions */}
      <div>
        <label className="font-semibold block mb-1">Normal Conditions?</label>
        <select
          value={normalConditions}
          onChange={(e) => setNormalConditions(e.target.value as "Yes" | "No")}
          className="border rounded px-2 py-1 w-full"
        >
          <option value="Yes">Yes</option>
          <option value="No">No</option>
        </select>
      </div>

      {/* Noise Entry Fields */}
      <div>
        <label className="font-semibold block mb-1">Noise Source</label>
        <input
          type="text"
          value={noiseEntry.source}
          onChange={(e) => setNoiseEntry({ ...noiseEntry, source: e.target.value })}
          className="border rounded px-2 py-1 w-full"
        />
      </div>
      <div>
        <label className="font-semibold block mb-1">Description</label>
        <textarea
          value={noiseEntry.description}
          onChange={(e) => setNoiseEntry({ ...noiseEntry, description: e.target.value })}
          className="border rounded px-2 py-1 w-full"
        />
      </div>
      <div>
        <label className="font-semibold block mb-1">Mitigation</label>
        <input
          type="text"
          value={noiseEntry.mit}
          onChange={(e) => setNoiseEntry({ ...noiseEntry, mit: e.target.value })}
          className="border rounded px-2 py-1 w-full"
        />
      </div>
      <div>
        <label className="font-semibold block mb-1">Type</label>
        <input
          type="text"
          value={noiseEntry.type}
          onChange={(e) => setNoiseEntry({ ...noiseEntry, type: e.target.value })}
          className="border rounded px-2 py-1 w-full"
          disabled={normalConditions === "No"}
        />
      </div>

      {/* Add Source Button */}
      <Button
        variant="primary"
        onClick={handleAddSource}
        disabled={!isAddEnabled}
      >
        + Add Source
      </Button>

      {/* List of Added Sources */}
      {noiseSources.length > 0 && (
        <div className="mt-4">
          <h4 className="font-semibold mb-2">Added Noise Sources</h4>
          <ul className="list-disc pl-6 space-y-1">
            {noiseSources.map((ns, idx) => (
              <li key={idx} className="flex justify-between items-center">
                <span>
                  {ns.source}  {ns.description} ({ns.type}) [{ns.mit}]
                </span>
                <div className="flex gap-2">
                  <Button variant="secondary" onClick={() => handleEdit(idx)}>
                    Edit
                  </Button>
                  <Button variant="danger" onClick={() => handleDelete(idx)}>
                    Delete
                  </Button>
                </div>
              </li>
            ))}
          </ul>
        </div>
      )}

      {/* Next Button */}
      <div className="flex justify-end mt-4">
        <Button
          variant="success"
          onClick={onNext}
          disabled={noiseSources.length === 0}
        >
          Next
        </Button>
      </div>
    </div>
  );
}

