export type Equipment = {
  id: string;
  type: "SLM" | "Calibrator" | "";
  name: string;
  serial: string;
  calibrationDate?: string;
  weighting: string;
  responseImpulse: string;
  responseLEQ: string;
  pre: string;
  during: string;
  post: string;
  areaRef: string;
  startDate?: string;
  endDate?: string;
};

export type Area = {
  id: string;
  name: string;
  process?: string;
  noiseLevelDb?: number;
  noiseType?: string;
  shiftDuration?: number;
  exposureTime?: number;
  notes?: string;
  detailsCompleted?: boolean; 
  subAreas?: Area[];
};

export type SurveyStatus = "In Progress" | "Completed" | "Submitted";

export type SurveyData = {
  id: string;
  client: string;
  project: string;
  site: string;
  surveyType: string;
  startDate: string;
  endDate: string;
  description: string;
  clientId?: string;
  projectId?: string;
  equipment: Equipment[];
  areas: Area[];
  status: SurveyStatus;
};

export type Step = 1 | 2 | 3 | 4 | 5;

// Empty survey template
export const emptySurvey: SurveyData = {
  id: "",
  client: "",
  project: "",
  site: "",
  surveyType: "",
  startDate: "",
  endDate: "",
  description: "",
  equipment: [],
  areas: [],
  status: "In Progress",
};

export interface Client {
  id: string;
  name: string;
}

export interface Project {
  id: string;
  clientId: string;
  name: string;
}






