// src/components/OHSimpleApp.tsx
import React, { useState, useRef, useEffect } from "react";
import { useMsal } from "@azure/msal-react";
import { SurveyData, Step, emptySurvey } from "./types";
import Container from "./common/Container";
import ProgressBar from "./common/ProgressBar";
import StartSurvey from "./StartSurvey";
import EquipmentEntry from "./EquipmentEntry";
import AreaAndNoise from "./AreaAndNoise";
import NoiseSources from "./NoiseSources";
import Summary from "./Summary";
import ConfirmDialog from "./common/ConfirmDialog";
import GijimaLogo from "./assets/Gijima-Logo.jpg";

export interface OHSimpleAppProps {
  user: { username: string };
  initialSurvey: SurveyData;
  onExit: (survey: SurveyData) => void;
  onSaveSurvey: (survey: SurveyData) => void;
}

export default function OHSimpleApp({
  user,
  initialSurvey,
  onExit,
  onSaveSurvey,
}: OHSimpleAppProps) {
  const { instance } = useMsal();
  const steps = ["Survey", "Equipment", "Areas & Noise", "Noise Sources", "Summary"];
  const [step, setStep] = useState<Step>(1);
  const [data, setData] = useState<SurveyData>(initialSurvey);
  const [selectedAreaPath, setSelectedAreaPath] = useState<{
    main: number;
    sub?: number;
    ss?: number;
  } | null>(null);

  const containerRef = useRef<HTMLDivElement>(null);

  // Confirmation dialogs state
  const [logoutConfirmOpen, setLogoutConfirmOpen] = useState(false);
  const [newSurveyConfirmOpen, setNewSurveyConfirmOpen] = useState(false);
  const [saveConfirmOpen, setSaveConfirmOpen] = useState(false);
  const [saveSuccessOpen, setSaveSuccessOpen] = useState(false);
  const [unsavedNoticeOpen, setUnsavedNoticeOpen] = useState(false);

  // Backup of survey before save
  const [backupSurvey, setBackupSurvey] = useState<SurveyData | null>(null);

  // Update survey state
  const patch = (patchData: Partial<SurveyData>) =>
    setData((d) => ({ ...d, ...patchData }));

  // Navigation
  const next = () => setStep((s) => (s < 5 ? ((s + 1) as Step) : s));
  const prev = () => setStep((s) => (s > 1 ? ((s - 1) as Step) : s));
  const gotoNoiseSources = () => setStep(4 as Step);

  // Start new survey
  const startNewSurvey = () => {
    setData({ ...emptySurvey, id: Date.now().toString() });
    setStep(1);
    setNewSurveyConfirmOpen(false);
    setSelectedAreaPath(null);
  };

  // Logout handler
  const handleLogout = () => {
    onExit(data);
    instance.logoutPopup();
    setLogoutConfirmOpen(false);
  };

  // Save handlers
  const handleSave = () => {
    setBackupSurvey({ ...data });
    setSaveConfirmOpen(true);
  };

  const confirmSave = () => {
    onSaveSurvey(data);
    setSaveConfirmOpen(false);
    setSaveSuccessOpen(true);
  };

  const undoSave = () => {
    if (backupSurvey) {
      setData(backupSurvey);
      onSaveSurvey(backupSurvey);
    }
    setSaveSuccessOpen(false);
    setUnsavedNoticeOpen(true);
  };

  useEffect(() => {
    containerRef.current?.scrollTo({ top: 0, behavior: "smooth" });
  }, [step]);

  return (
    <div ref={containerRef} className="min-h-screen bg-gray-50">
      <Container>
        {/* Header */}
        <div className="flex justify-between items-center p-4 border-b bg-white shadow-sm">
          <div className="flex items-center space-x-3">
            <img src={GijimaLogo} alt="Gijima Logo" className="h-12 w-auto" />
          </div>
          <div className="flex gap-2">
            <button
              onClick={() => setNewSurveyConfirmOpen(true)}
              className="px-4 py-2 rounded-md bg-blue-600 text-white text-sm font-medium hover:bg-blue-700 transition"
            >
              Start New Survey
            </button>
            <button
              onClick={() => setLogoutConfirmOpen(true)}
              className="px-4 py-2 rounded-md bg-red-600 text-white text-sm font-medium hover:bg-red-700 transition"
            >
              Logout
            </button>
          </div>
        </div>

        {/* Progress */}
        <ProgressBar step={step} totalSteps={5} steps={steps} />

        {/* Steps */}
        {step === 1 && (
          <StartSurvey
            data={data}
            onChange={patch}
            onNext={() => setStep(2 as Step)}
            onBack={() => onExit(data)}
            onSave={handleSave}
          />
        )}

        {step === 2 && (
          <EquipmentEntry
            data={data}
            onChange={patch}
            onPrev={() => setStep(1 as Step)}
            onNext={() => setStep(3 as Step)}
            onSave={handleSave}
            onDetails={gotoNoiseSources}
          />
        )}

        {step === 3 && (
          <AreaAndNoise
            data={data}
            onChange={patch}
            onPrev={() => setStep(2 as Step)}
            onNext={() => setStep(5 as Step)}
            onSave={handleSave}
            onOpenDetails={(path) => {
              setSelectedAreaPath(path); // store area/sub/sub-sub
              setStep(4 as Step); // navigate to NoiseSources
            }}
          />
        )}

        {step === 4 && (
          <NoiseSources
            data={data}
            onChange={patch}
            onBack={() => setStep(3 as Step)}
            onNext={() => setStep(5 as Step)}
            onSave={handleSave}
            selectedAreaPath={selectedAreaPath}
          />
        )}

        {step === 5 && (
          <Summary data={data} onPrev={() => setStep(4 as Step)} onReset={startNewSurvey} />
        )}

        {/* Confirmation Dialogs */}
        <ConfirmDialog
          open={logoutConfirmOpen}
          title="Confirm Logout"
          message="Are you sure you want to log out?"
          confirmText="Logout"
          onConfirm={handleLogout}
          onCancel={() => setLogoutConfirmOpen(false)}
        />
        <ConfirmDialog
          open={newSurveyConfirmOpen}
          title="Start New Survey"
          message="This will clear the current form. Do you want to continue?"
          confirmText="Start New"
          onConfirm={startNewSurvey}
          onCancel={() => setNewSurveyConfirmOpen(false)}
        />
        <ConfirmDialog
          open={saveConfirmOpen}
          title="Save Survey"
          message="Do you want to save this survey?"
          confirmText="Save"
          onConfirm={confirmSave}
          onCancel={() => setSaveConfirmOpen(false)}
        />
        <ConfirmDialog
          open={saveSuccessOpen}
          title="Survey Saved"
          message=" Survey saved successfully!"
          confirmText="OK"
          onConfirm={() => setSaveSuccessOpen(false)}
          onCancel={undoSave}
        />
        <ConfirmDialog
          open={unsavedNoticeOpen}
          title="Survey Not Saved"
          message=" Your survey changes were not saved."
          confirmText="OK"
          onConfirm={() => setUnsavedNoticeOpen(false)}
          onCancel={() => setUnsavedNoticeOpen(false)}
        />
      </Container>
    </div>
  );
}
