// src/components/NoiseSources.tsx
import React, { useState } from "react";
import { SurveyData } from "./types";
import Section from "./common/Section";
import Button from "./common/Button";
import NoiseSourcesForm from "./NoiseForm";

interface NoiseSourcesProps {
  data: SurveyData;
  onChange: (patch: Partial<SurveyData>) => void;
  onBack: () => void;
  onNext: () => void;
  onSave: () => void;
  selectedAreaPath?: { main: number; sub?: number; ss?: number } | null;
}

export default function NoiseSources({
  data,
  onChange,
  onBack,
  onNext,
  onSave,
  selectedAreaPath,
}: NoiseSourcesProps) {
  const [normalConditions, setNormalConditions] = useState<"Yes" | "No">("No");
  const [noiseEntry, setNoiseEntry] = useState({
    source: "",
    description: "",
    mit: "",
    type: "",
  });
  const [noiseSources, setNoiseSources] = useState<
    { source: string; description: string; mit: string; type: string }[]
  >([]);

  const isValid = noiseSources.length > 0;

  return (
    <Section title="">
      {/* Title */}
      <h2 className="text-xl font-bold text-center mb-6">Noise Sources</h2>

      {/* Noise Sources Form */}
      <NoiseSourcesForm
        normalConditions={normalConditions}
        setNormalConditions={setNormalConditions}
        noiseEntry={noiseEntry}
        setNoiseEntry={setNoiseEntry}
        noiseSources={noiseSources}
        setNoiseSources={setNoiseSources}
        onNext={onNext}
        selectedAreaPath={selectedAreaPath}
      />

      {/* Navigation Buttons */}
      <div className="flex justify-between mt-8">
        <Button variant="secondary" onClick={onBack}>
          Back
        </Button>
        <div className="flex gap-2">
          <Button onClick={onSave} disabled={!isValid} variant="primary">
            Save
          </Button>
          <Button onClick={onNext} disabled={!isValid} variant="success">
            Next
          </Button>
        </div>
      </div>
    </Section>
  );
}
